: 0 2px 4px 0 rgba(0, 0, 0, 0.2);\r\n}\r\n\r\n.app-bar .app-bar__menu {\r\n  display: flex;\r\n  align-items: center;\r\n}\r\n\r\n.app-bar .app-bar__menu button {\r\n  background-color: transparent;\r\n  border: none;\r\n  font-size: 18px;\r\n  padding: 20px;\r\n  cursor: pointer;\r\n}\r\n\r\n.app-bar .app-bar__brand {\r\n  display: flex;\r\n  align-items: center;\r\n}\r\n\r\n.app-bar .app-bar__brand h1 {\r\n  color: white;\r\n  text-transform: uppercase;\r\n  font-size: 13px;\r\n  user-select: none;\r\n  font-family: 'Lobster', cursive;\r\n}\r\n\r\n.app-bar .app-bar__navigation {\r\n  position: absolute;\r\n  top: 50px;\r\n  left: -180px;\r\n  width: 150px;\r\n  transition: all 0.3s;\r\n  padding: 8px;\r\n  background-color: rgba(64, 198, 238, 1) 55.2%;\r\n  overflow: hidden;\r\n  color: white;\r\n}\r\n\r\n.app-bar .app-bar__navigation.open {\r\n  left: 0;\r\n}\r\n\r\n.app-bar .app-bar__navigation ul li a {\r\n  display: inline-block;\r\n  text-decoration: none;\r\n  color: black;\r\n  padding: 10px 10px 10px 10px;\r\n  margin-bottom: 5px;\r\n  width: 100%;\r\n  background-color: rgb(60, 147, 202);\r\n}\r\n\r\n/*\r\n  Main Content\r\n*/\r\n\r\nmain {\r\n  padding: 32px;\r\n  flex: 1;\r\n}\r\n\r\n.content {\r\n  margin: 0 auto;\r\n  min-height: 100%;\r\n}\r\n\r\n.content .content__heading {\r\n  font-weight: normal;\r\n  text-align: center;\r\n  padding: 44px;\r\n}\r\n\r\n/*\r\n  restaurants\r\n*/\r\n\r\n.restaurants {\r\n  display: grid;\r\n  grid-template-columns: 1fr;\r\n  gap: 16px;\r\n}\r\n\r\n/*\r\n  restaurant Item\r\n*/\r\n\r\n.restaurant-item {\r\n  width: 100%;\r\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\r\n  border-radius: 8px;\r\n  overflow: hidden;\r\n}\r\n\r\n.restaurant-item__header {\r\n  position: relative;\r\n}\r\n\r\n.restaurant-item .restaurant-item__header .restaurant-item__header__poster {\r\n  width: 100%;\r\n}\r\n\r\n.restaurant-item .restaurant-item__header .restaurant-item__header__rating {\r\n  position: absolute;\r\n  padding: 8px;\r\n  bottom: 20px;\r\n  left: 0;\r\n  display: inline-block;\r\n  background-color: black;\r\n  color: white;\r\n}\r\n\r\n.restaurant-item\r\n  .restaurant-item__header\r\n  .restaurant-item__header__rating\r\n  .restaurant-item__header__rating__score {\r\n  margin-left: 10px;\r\n}\r\n\r\n.restaurant-item .restaurant-item__content {\r\n  padding: 16px;\r\n}\r\n.restaurant-item:hover {\r\n  box-shadow: 10px 10px 5px 0 #8ccbf0;\r\n}\r\n\r\n.restaurant-item .restaurant-item__content h3 {\r\n  margin: 0 0 10px 0;\r\n}\r\n\r\n.restaurant-item .restaurant-item__content h3 a {\r\n  color: #db0000;\r\n  text-decoration: none;\r\n  padding: 15px 15px 15px 15px;\r\n}\r\n\r\n.restaurant-item .restaurant-item__content p {\r\n  overflow: hidden;\r\n  text-overflow: ellipsis;\r\n  display: -webkit-box;\r\n  -webkit-line-clamp: 4; /* number of lines to show */\r\n  -webkit-box-orient: vertical;\r\n}\r\n\r\n/*\r\n  restaurant\r\n*/\r\n\r\n.restaurant {\r\n  margin: 0 auto;\r\n  width: 100%;\r\n  max-width: 1200px;\r\n  display: grid;\r\n  grid-template-columns: 1fr;\r\n  gap: 18px 16px;\r\n  background-color: #dbc9ab;\r\n}\r\n\r\n.restaurant .restaurant__poster {\r\n  width: 100%;\r\n  grid-column-start: 1;\r\n  grid-column-end: 5;\r\n}\r\n\r\n.restaurant .restaurant__info h4 {\r\n  margin: 8px 0;\r\n}\r\n.restaurant .restaurant__info h3 {\r\n  background-color: #dbc9ab;\r\n}\r\n.restaurant__info {\r\n  box-shadow: 0 4px 8px 0 #dbc9ab;\r\n  text-align: center;\r\n}\r\n.restaurant__description {\r\n  box-shadow: 0 4px 8px 0 #dbc9ab;\r\n}\r\n.restaurant__description h3 {\r\n  background-color: #dbc9ab;\r\n}\r\n\r\n/*\r\n  like\r\n*/\r\n.like {\r\n  width: 55px;\r\n  height: 55px;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  background-color: #db0000;\r\n\r\n  position: fixed;\r\n  bottom: 16px;\r\n  right: 16px;\r\n  border-radius: 50%;\r\n  border: 0;\r\n\r\n  font-size: 18px;\r\n  color: white;\r\n  cursor: pointer;\r\n}\r\n\r\n/*\r\n  Footer\r\n*/\r\n\r\nfooter {\r\n  padding: 16px;\r\n  text-align: center;\r\n  border-top: 3px groove black;\r\n  background-image: linear-gradient(\r\n    65.9deg,\r\n    rgba(85, 228, 224, 1) 5.5%,\r\n    rgba(75, 68, 224, 0.74) 54.2%,\r\n    rgba(64, 198, 238, 1) 55.2%,\r\n    rgba(177, 36, 224, 1) 98.4%\r\n  );\r\n}\r\n\r\n/* FORM */\r\nform {\r\n  margin: 2em 0;\r\n  padding: 50px;\r\n  border: 3px dotted #dbc9ab;\r\n  border-radius: 0 15px 0 15px;\r\n}\r\n\r\n.form-control {\r\n  display: block;\r\n  width: 100%;\r\n  min-height: 1em;\r\n  padding: 15px 15px 15px 15px;\r\n  font-size: 1em;\r\n  font-weight: normal;\r\n  color: black;\r\n  background-color: #fff;\r\n  appearance: none;\r\n  outline: none;\r\n  border: 1px solid transparent;\r\n  border-bottom: 1px solid #dbc9ab;\r\n}\r\n\r\n.form-label {\r\n  padding-bottom: 10px;\r\n  font-weight: bold;\r\n  font-style: normal;\r\n  font-size: small;\r\n  color: crimson;\r\n}\r\n\r\n.mb-3 {\r\n  margin-bottom: 1.3em;\r\n  margin-top: 0.5em;\r\n}\r\n\r\n.submit-btn {\r\n  padding: 15px 15px 15px 15px;\r\n  font-weight: 700;\r\n  color: white;\r\n  text-align: center;\r\n  vertical-align: middle;\r\n  background-color: crimson;\r\n  border: 1px solid transparent;\r\n  font-size: large;\r\n  cursor: pointer;\r\n  text-decoration: none;\r\n}\r\n\r\n.btn:hover,\r\n.submit-btn:hover {\r\n  background-color: #f0e68c;\r\n}\r\n\r\n/* DETAIL DRINK */\r\n\r\n.detail-drink {\r\n  display: flex;\r\n  flex-direction: column;\r\n  text-align: center;\r\n  align-content: center;\r\n}\r\n\r\n.detail-drink li {\r\n  padding: 0.5em 0;\r\n  display: block;\r\n  text-decoration: none;\r\n  border: 0.5px solid #dbc9ab;\r\n  border-width: 0 0 0.5px;\r\n}\r\n\r\n/* DETAIL FooD */\r\n\r\n.detail-food {\r\n  display: flex;\r\n  flex-direction: column;\r\n  text-align: center;\r\n  align-content: center;\r\n}\r\n\r\n.detail-food li {\r\n  padding: 0.5em 0;\r\n  display: block;\r\n  text-decoration: none;\r\n  background-color: #dbc9ab;\r\n  border: 0.5px solid #f0e3ca;\r\n  border-width: 0 0 0.5px;\r\n}\r\n\r\n/* DETAIL REVIEW */\r\n.detail-review {\r\n  max-width: 100%;\r\n  font-size: 1em;\r\n  text-align: center;\r\n}\r\n\r\n.detail-review-item {\r\n  color: black;\r\n  background-color: #dbc9ab;\r\n  margin-bottom: 1.5em;\r\n  padding: 1em 1em;\r\n}\r\n\r\n.detail-review-item:last-child {\r\n  margin-bottom: 0;\r\n}\r\n\r\n.review-header {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  padding: 1em 1em;\r\n  color: crimson;\r\n  border-bottom: 1px solid black;\r\n}\r\n\r\n.review-name {\r\n  font-weight: bold;\r\n  display: flex;\r\n  align-items: center;\r\n}\r\n\r\n.review-date {\r\n  font-size: 0.8em;\r\n  font-weight: lighter;\r\n}\r\n\r\n.review-body {\r\n  padding: 1em;\r\n  text-align: left;\r\n}\r\n\r\n#query {\r\n  padding: 15px 15px 15px 15px;\r\n}\r\n\r\n.color {\r\n  background-color: #8ccbf0;\r\n}\r\n"],"sourceRoot":""}]);
